
import ConfigButton from "@/components/ui/ConfigButton.vue";
import ModalWindow from "@/components/common/ModalWindow.vue";
import {defineComponent} from "vue";
import Button from "@/components/ui/Button.vue";

export default defineComponent({
	name: "EditCharacterInfo",
	components: {Button, ModalWindow, ConfigButton},
	data() {
		return {
			modal: false,
			name: '',
			race: '',
			luck: 0
		}
	},
	computed: {
		isValidData(): boolean {
			return !!(this.name && this.race)
		},
	},
	watch:{
		modal(value) {
			if (!value) {
				return
			}
			this.luck = this.$store.state.character.luck
			this.name = this.$store.state.character.name
			this.race = this.$store.state.character.race
		}
	},
	methods: {
		close() {
			this.modal = false;
			this.name = ''
			this.race = ''
			this.luck = 0
		},
		save() {
			this.$store.commit('updateName', this.name)
			this.$store.commit('updateRace', this.race)
			this.$store.commit('updateLuck', this.luck)
			this.modal = false;
		}
	}
})
